.createEmp-bg img {
    width: 100%;
    max-width: 400px;
    height: auto;
}
.createEmp-bg {
    width: 35%;
}
.create-emp-container {
    width: 100%;
    margin: auto;
    display: flex;
    flex-direction: row;
    padding: 30px 0;
}
.create-emp-form {
    width: 50%;
    margin: auto;
    max-width: 350px;
}
.err-msg {
    padding: 5px;
    color: red;
    text-align: center;
}

/* responsive */
@media only screen and (max-width: 767px) {
    .create-emp-container {
        flex-direction: column-reverse;
    }
    .createEmp-bg{
        margin: 30px auto;
    }
    .create-emp-form {
        margin: 0 auto;
        width: 80%;
    }
}